<template>
  <div class="cameras">

    <b-row>
      <b-col>
        <h1>Подключенные камеры</h1>
      </b-col>
      <b-col>
          <b-button variant="dark" class="float-right mt-2" to="/addCamera">Добавить камеру</b-button>
      </b-col>
    </b-row>
    <div v-if="cameras.length == 0">
      <h3>Не найдено ни одной камеры</h3>
    </div>
    <div class="p-2">
      <b-card class="cameraStream m-2 p-0" v-for="camera in cameras" :key="camera.id" v-on:click="goToCam(camera.id)">
          <img width="420px" src="../assets/img.png">
          <b-icon class="trash-icon m-0 h5" variant="danger" icon="trash-fill" v-on:click="deleteCam(camera.id)"/>
      </b-card>
    </div>
  </div>
</template>
<script>
export default {
  name: 'cameras',
  data () {
    return {
      cameras: [
        {id: 0, username: 'admin', password: 'admin', address: ''},
        {id: 1, username: 'admin', password: 'admin', address: ''},
        {id: 2, username: 'admin', password: 'admin', address: ''},
        {id: 3, username: 'admin', password: 'admin', address: ''},
        {id: 4, username: 'admin', password: 'admin', address: ''},
      ],
    }
  },
  methods: {
    async deleteCam(id){
      if (confirm('Вы уверены, что хотите удалить камеру '+ id+'?')){
        alert('Камера '+ id+' удалена.')
      }
    },
    async goToCam(id){
      this.$router.push({name: 'CameraPage', params: {id: id}})
    }
  }
}
</script>


